body {
	background-color: white;
	background-position: center;
	background-size: cover;
	background-attachment: fixed;
	margin: 0;
	padding: 0;
}

.App {
	font-family: -apple-system, arial, monospace;
	font-weight: bolder;
}

.button {
	background-color: teal;
	color: white;
	border: none;
	border-radius: 7px;
	font-weight: bolder;
	padding: 5px 15px;
	font-family: monospace;
	cursor: pointer;
}